<template>
	<div class="page">
		<ul class="problem-list">
			<li v-for="(item,index) in items" v-on:click="answers(index)">
				<mt-cell :title="item.question" is-link></mt-cell>
				<transition name="show" mode="out-in">
					<div class="answer" v-show="item.flag">{{item.answer}}</div>
				</transition>
			</li>
		</ul>
	</div>
</template>

<script>
	// let html = document.documentElement;
	// window.rem = html.getBoundingClientRect().width / 25 ;
	// html.style.fontSize = window.rem + 'px';
	export default {
		data () {
			return {
				items:[
					{
						question:'cvn2',
						answer:'cvn2',
						flag:false
					},{
						question:'',
						answer:'12',
						flag:false
					},{
						question:'',
						answer:'',
						flag:false
					},{
						question:'',
						answer:'',
						flag:false
					},{
						question:'',
						answer:'',
						flag:false
					},{
						question:'',
						answer:'',
						flag:false
					}
				]
			}
		},
		methods:{
			answers: function(index){
				for(var i = 0, l = this.items.length; i<l; i++){
					if (i == index) {
						this.items[i].flag = !this.items[i].flag;
					} else {
						this.items[i].flag = false;
					}
				}
			}
		}
	}
</script>
	
<style scoped>
	.page{
		background: #fff;
	}
	.answer{
		padding: 1rem;
		color: #999;
		border-bottom: 1px solid #f0f0f0
	}
	.show-enter-active,
	.show-leave-active {
		transition: all .2s
	}
	.show-enter{
		opacity: 0;
		transform: translateY(-10px);
	}
</style>


