<template>
	<section>
		<!-- <mt-header fixed :title="this.$route.name"></mt-header> -->
		<div class="page">
			<form name="signupForm" class="signup-form">
				<div class="field-group">
					<mt-cell title="">
					  <span><i></i>{{form.amount}}</span>
					</mt-cell>
					<mt-cell title="">
					  <span>{{form.parts}}</span>
					</mt-cell>
				</div>
				<div class="field-group">
					<mt-field label="" placeholder="" type="text" v-model="form.name"></mt-field>
					<mt-field label="" placeholder="" type="number" v-model="form.cardNo"></mt-field>
					<mt-field label="" placeholder="" type="number" v-model="form.accNo"></mt-field>
					<mt-field label="" placeholder="/" type="number" v-model="form.validDate"></mt-field>
					<mt-field label="cvn2" placeholder="" type="number" v-model="form.cvn"></mt-field>
					<mt-field label="" placeholder="" type="tel" v-model="form.reserveMobile"></mt-field>
				</div>
				<div class="field-group">
					<div class="clearfix">
						<mt-checklist
						  v-model="form.agree"
						  :options="['']">
						</mt-checklist>
						<router-link to="/contract" class="server-link"></router-link>
					</div>
					<mt-button type="primary" size="large" class="signup-button" @click.prevent="onSubmit"></mt-button>
				</div>
			</form>
		</div>
	</section>
</template>
<script>
import { createOrder } from '../api'
import Validate from '../assets/js/WxValidate'
export default {
	data () {
		return {
			form: {
				amount: '',
	      parts: '',
				name: '',
				cardNo: '',
				accNo: '',
				validDate: '',
				cvn: '',
				reserveMobile: '',
	      amount: '',
	      parts: '',
	      sessionId: '',
	      agree: []
			}
		}
	},
	methods: {
		showToast (msg) {
			this.$toast({
			  message: msg,
			  duration: 2000
			})
		},
	  initValidate() {
	  	let self = this
	    this.validate = new Validate({
	    	name: {
	        required: true,
	      },
	      cardNo: {
	        required: true,
	        idcard: true
	      },
	      accNo: {
	      	required: true
	      },
	      validDate: {
	      	required: true
	      },
				cvn: {
					required: true
				},
	      reserveMobile: {
	        required: true,
	        tel: true, 
	      },
	      agree: {
	        required: true
	      },
	    }, {
	      name: {
	        required: ''
	      },
	      cardNo: {
	        required: ''
	      },
	      accNo: {
	      	required: ''
	      },
	      validDate: {
	      	required: ''
	      },
				cvn: {
					required: 'cvn2'
				},
	      reserveMobile: {
	        required: '',
	        tel: ''
	      },
	      agree: {
	        required: ''
	      },
	    })
    },
		onSubmit () {
			if (!this.validate.checkForm(this.form)) {
	      const error = this.validate.errorList[0]
	      this.showToast(error.msg)
	      return false
	    }
	    let shopMechanism = JSON.parse(sessionStorage.getItem('shopMechanism'))
	    let data = {
	    	amount: this.form.amount,
	      parts: parseInt(this.form.parts),
				name: this.form.name,
				cardNo: this.form.cardNo,
				accNo: this.form.accNo,
				validDate: this.form.validDate,
				cvn: this.form.cvn,
				reserveMobile: this.form.reserveMobile,
	      customerSessionId: sessionStorage.getItem('sessionId'),
    		shopId: shopMechanism.shopId,
    		mechanismId: shopMechanism.mechanismId
	    }
	    console.log(data)
			createOrder(data).then(res => {
				console.log(res)
				if(res.data.code === 0) {
					this.showToast('')
					this.$router.push({ path: '/records' })
				} else {
					this.showToast(res.data.message)
				}
			}).catch(function (error) {
        console.log(error)
      })
		},
	},
	mounted () {
		this.initValidate()
		this.form.amount = sessionStorage.getItem('amount')
		this.form.parts = sessionStorage.getItem('parts')
	}
}
</script>
<style scoped>
	.mint-checklist, .server-link {
		float: left;
	}
	.server-link {
		color: #34c1ea;
	}
	.server-link,
	.signup-button {
		margin-top: 10px;
	}
	i {
		font-weight: bold;
		font-style: normal
	}
</style>


